import React, { ReactElement, useState } from 'react'
import LogoImg from '../../Images/company.png';
import {Link} from 'react-router-dom'
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Icon from '@material-ui/core/Icon';

import HomeIcon from '@material-ui/icons/HomeRounded'
import AboutUsIcon from '@material-ui/icons/InfoRounded'
import ContactUsIcon from '@material-ui/icons/ContactSupportRounded'
import MyDrugsIcon from '@material-ui/icons/SearchRounded'
import SearchDrugsIcon from '@material-ui/icons/SearchSharp'

import UserIcon from '@material-ui/icons/PersonAddOutlined'
import AcountIconOutlined from '@material-ui/icons/AccountCircleOutlined'

import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Switch from '@material-ui/core/Switch';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormGroup from '@material-ui/core/FormGroup';
import MenuItem from '@material-ui/core/MenuItem';
import Hidden from '@material-ui/core/Hidden'
import Menu from '@material-ui/core/Menu';

import { Box, Button, Divider, List, ListItem, ListItemText, ListItemIcon, ListSubheader, Typography, Fade } from '@material-ui/core';
import Drawer from '@material-ui/core/Drawer'

import {credentials} from '../../config'
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    menuButton: {
      marginLeft: theme.spacing(2),
    },
    title: {
      flexGrow: 1,
    },
    logo:{
      height:35
    },
    buttonDivider:{
      width:15,
      background:'transparent'
    },
    appBarButton:{
      fontSize:15,
      margin:'auto',
      '& .buttonText':{
        marginRight:theme.spacing(1),
        textAlign:'center',
        width:70
      }
    },
  }),
);

interface IProps {
  
}

export default function Header({}: IProps): ReactElement {
  const classes = useStyles();
  const auth=credentials.auth;
  const [openDrawer,setOpenDrawer]=useState(false);
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const unAuthListItems=[
    {text:'',to:'/'},
    {text:' ',to:'/aboutUs'},
    {text:' ',to:'/contactUs'}
  ]
  const authListItems=[
    ...unAuthListItems,
    {text:' ',to:'/myLazDrugs'},
    {text:'  ',to:'searchDrugs'}
  ]
  const listItems =auth?authListItems:unAuthListItems;

  const handleMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };
  const handleDrawerToggle=()=>{
    setOpenDrawer(!openDrawer);
  }
  return (
       <AppBar position="sticky" color="inherit">
         <Toolbar variant="dense">
           <Box m={1} alignItems="center">          
            <Link to="/">
              <img  src={LogoImg} alt=" " className={classes.logo}/>
            </Link>
           </Box>
           {/**show only desktop screen */}
           <Hidden smDown={true}>
            <Box flexGrow={1}>
              <Button component={Link} to="/" variant="text"  color="primary" className={classes.appBarButton}></Button>
              <Button component={Link} to="/aboutUs" variant="text"  color="primary" className={classes.appBarButton}> </Button>
              <Button component={Link} to="/contactUs" variant="text"  color="primary" className={classes.appBarButton}> </Button>
              {auth && <>
                <Button component={Link} to="/myLazDrugs" variant="text"  color="primary" className={classes.appBarButton}> </Button>
                <Button component={Link} to="/searchDrugs" variant="text"  color="primary" className={classes.appBarButton}>  </Button>
              </>}
            </Box>
              {auth && (
                <Box>
                  <IconButton
                    aria-label="account of current user"
                    aria-controls="menu-appbar"
                    aria-haspopup="true"
                    onClick={handleMenu}
                    color="primary"
                  >
                    <AccountCircle />
                  </IconButton>
                  <Menu
                    id="menu-appbar"
                    anchorEl={anchorEl}
                    anchorOrigin={{
                      vertical: 'bottom',
                      horizontal: 'right',
                    }}
                    keepMounted
                    transformOrigin={{
                      vertical: 'bottom',
                      horizontal: 'right',
                    }}
                    open={open}
                    TransitionComponent={Fade}
                    onClose={handleMenuClose}
                  >
                    <MenuItem component={Link} to="/profile" onClick={handleMenuClose}>
                      <UserIcon/>
                      <Typography variant="subtitle2"></Typography>
                    </MenuItem>
                    <MenuItem component={Link} to="/account" onClick={handleMenuClose}>
                      <AcountIconOutlined/>
                      <Typography variant="subtitle2"></Typography>
                    </MenuItem>
                  </Menu>
                </Box>
              )}
              {!auth && 
              <Box display="flex">
                  <Button  component={Link} to="/join" size="medium" color="primary" 
                        variant="outlined"                   
                        className={classes.appBarButton} 
                        startIcon={<UserIcon  fontSize="small"/> }> 
                  <span className="buttonText">   </span>
                  </Button>
                  <Divider className={classes.buttonDivider} orientation="vertical" flexItem/>
                  <Button component={Link} to="/login" size="medium" color="primary" 
                          variant="outlined"                   
                          className={classes.appBarButton} 
                          startIcon={<Icon  fontSize="small" className="fa fa-sign"/> }> 
                      <span className="buttonText">  </span>
                  </Button>
              </Box>                 
              }
           </Hidden>
           {/**show only for mobile screen */}
           <Hidden mdUp={true}>
            <Box flexGrow={1}/>
            <IconButton edge="start" 
                        color="primary"
                        aria-label="open drawer"
                        onClick={handleDrawerToggle}
                        className={classes.menuButton}>
              <MenuIcon />
            </IconButton>
            <Drawer anchor="left" open={openDrawer} 
                    dir="rtl"
                    onClick={handleDrawerToggle}>
              <div role="presentation"
                   onClick={handleDrawerToggle}
                   onKeyDown={handleDrawerToggle}>
                 <List>
                   {listItems.map((item,index)=>(
                     <ListItem button key={index} component={Link} to={item.to}>
                         <ListItemIcon>
                            {index===0 && <HomeIcon/>}
                            {index===1 && <AboutUsIcon/>}
                            {index===2 && <ContactUsIcon/>}
                            {index===3 && <MyDrugsIcon/>}
                            {index===4 && <SearchDrugsIcon/>}
                         </ListItemIcon>
                         <ListItemText primary={item.text}/>
                     </ListItem>
                   ))}
                 </List>
                 <Divider/>
                 {!auth &&
                  <List>
                    <ListItem>
                        <Button  component={Link} to="/join"   color="primary" 
                              variant="contained"                   
                              className={classes.appBarButton}
                              startIcon={<Icon  fontSize="small" className="fa fa-user-plus"/> }> 
                            <span className="buttonText">   </span>
                        </Button>
                    </ListItem>
                    <ListItem>
                        <Button component={Link} to="/login"   color="primary" 
                                variant="contained"                   
                                className={classes.appBarButton} 
                                startIcon={<Icon  fontSize="small" className="fa fa-sign"/> }> 
                            <span className="buttonText">  </span>
                        </Button>
                    </ListItem>
                  </List>
                 }
                 {auth &&
                  <List                       
                       subheader={
                          <ListSubheader>
                            <Typography variant="subtitle1">
                                  
                            </Typography>
                          </ListSubheader>
                        }>
                    <ListItem>
                      <ListItemIcon>
                         <UserIcon/>
                      </ListItemIcon>
                      <ListItemText>
                        <Button component={Link} to="/profile" color="primary"></Button>
                      </ListItemText>
                    </ListItem>
                    <ListItem>
                      <ListItemIcon>
                         <AcountIconOutlined/>
                      </ListItemIcon>
                      <ListItemText>
                        <Button component={Link} to="/account" color="primary"></Button>
                      </ListItemText>
                    </ListItem>
                  </List>
                 }
              </div>
            </Drawer>
           </Hidden>
         </Toolbar>
       </AppBar>
  );
}
